* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.box {
  width: 100%;
  height: 100vh;
  background-image: url(https://wallpapercave.com/wp/nsFJoNv.jpg);
  background-size: 100% 100%;
  position: relative;
}

/* .back-image {
  width: 100%;
  height: 100%;
  
} */

.image {
  position: absolute;
  left: 0;
  bottom: 6vh;
  animation-name: mario-run;
  animation-duration: 6000ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes mario-run {
  0% {
    left: 0%;
  }
  100% {
    left: 88%;
  }
}

h1 {
  font-size: 70px;
  font-weight: bold;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: white;
  text-shadow: 10px 10px 5px darkgray;
  position: absolute;
  left: 40%;
  top: 5vh;
  animation: mario-text 4s linear infinite;
}

@keyframes mario-text {
  0% {
    font-size: 70px;
  }
  100% {
    font-size: 100px;
  }
}

ul {
  position: absolute;
  display: flex;
  left: 40%;
  bottom: 7vh;
}

ul li {
  background-color: darkmagenta;
  list-style: none;
  width: 50px;
  height: 50px;
  margin: 0px 50px;
  border-radius: 50%;
  animation: bounce 2s linear infinite;
}

@keyframes bounce {
  0% {
    transform: translateY(0);
    background-color: white;
  }
  50% {
    transform: translateY(-350px);
    background-color: red;
  }
  100% {
    transform: translateY(0);
    background-color: white;
  }
}

ul li:nth-child(1) {
  animation-delay: 0s;
  background-color: darkorange;
}

ul li:nth-child(2) {
  animation-delay: 0.2s;
  background-color: red;
}

ul li:nth-child(3) {
  animation-delay: 0.4s;
  background-color: blue;
}

ul li:nth-child(4) {
  animation-delay: 1s;
  background-color: green;
}

ul li:nth-child(5) {
  animation-delay: 1.4s;
  background-color: purple;
}
